import types from './types'
const addBrand = item => ({
    type: types.ADD_BRAND,
    item
})
const addModel = item => ({
    type: types.ADD_MODEL,
    item
})
const addFuel = item => ({
    type: types.ADD_FUEL,
    item
})
const clearBrands = () => ({
    type: types.CLEAR_BRANDS
})
const clearModels = () => ({
    type: types.CLEAR_MODELS
})
const clearFuels = () => ({
    type: types.CLEAR_FUELS

})

export default {
    addBrand,
    addModel,
    addFuel,
    clearBrands,
    clearModels,
    clearFuels,
}